<template>
    <v-row class="justify-center">
        <v-col cols="6" class="d-print-none">
            <v-toolbar class="my-2" flat outlined>
                <v-toolbar-title>
                    Statement for {{ statementTitle }}
                </v-toolbar-title>
                <v-spacer />
                <v-btn
                    :color="getAppColors.primary"
                    class="mx-2 white--text"
                    @click="downloadPdf"
                >
                    <v-icon left> mdi-download </v-icon>
                    PDF
                </v-btn>
                <v-btn
                    :color="getAppColors.primary"
                    class="white--text"
                    @click="printReceipt"
                >
                    <v-icon left> mdi-printer </v-icon>
                    Print
                </v-btn>
            </v-toolbar>
            <v-card :loading="loading">
                <v-skeleton-loader
                    v-if="loading"
                    type="list-item-avatar, article, article"
                    min-height="350px"
                />
                <v-card-text v-else ref="document">
                    <div v-html="statementDetail"></div>
                </v-card-text>
            </v-card>
        </v-col>
        <v-col cols="12" class="d-none d-print-flex">
            <v-card tile flat max-height="95%">
                <v-card-text>
                    <div v-html="statementDetail"></div>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>

<script>
import axios from 'axios';
import html2pdf from 'html2pdf.js';
import { mapGetters } from 'vuex';

export default {
    name: 'StatementDetail',
    props: {
        statementId: {
            type: String,
            required: true,
            default: '',
        },
        statementTitle: {
            type: String,
            required: true,
            default: '',
        },
    },
    data() {
        return {
            loading: false,
            statementDetail: null,
        };
    },
    computed: {
        ...mapGetters(['getAppColors', 'getInternalMxController']),
    },
    mounted() {
        this.getStatementDetail();
    },
    methods: {
        async getStatementDetail() {
            this.loading = true;

            try {
                let response = await axios.get(
                    `${this.getInternalMxController + '/get_statement_detail'}`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        params: {
                            statement_id: this.statementId,
                            statement_type: 'key2',
                        },
                    },
                );

                this.loading = false;

                console.log(response);

                if (response.data.success === true) {
                    let cleanData = response.data.data.replace(
                        /(<style[\w\W]+style>)/g,
                        '',
                    );

                    this.statementDetail = cleanData;
                } else {
                    console.log('No statement detail found');
                }
            } catch (error) {
                console.error('Could not get statement detail');
                console.error(error);
            }
        },
        downloadPdf() {
            html2pdf(this.$refs.document, {
                margin: [0, 1, 0, 1],
                filename: 'statement.pdf',
                image: { type: 'jpeg', quality: 0.98 },
                html2canvas: { dpi: 192, letterRendering: true },
                jsPDF: {
                    unit: 'in',
                    format: 'letter',
                    orientation: 'portrait',
                },
            });
        },
        printReceipt() {
            window.print();
        },
    },
};
</script>

